import React from 'react'
import PropTypes from 'prop-types'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import Color from '../../themes/Colors';
import {
  faHeart,
  faPlus,
  faTrash,
  faPencilAlt,
  faPlay,
  faTimes,
  faCheck,
  faChevronLeft
} from '@fortawesome/free-solid-svg-icons'

const AwesomeIcon = {
  heart: faHeart,
  plus: faPlus,
  trash: faTrash,
  edit: faPencilAlt,
  play: faPlay,
  close: faTimes,
  checked: faCheck,
  back: faChevronLeft
}

function Icon(props) {
  const {name, color, className} = props;
  return <FontAwesomeIcon
    className={className}
    icon={AwesomeIcon[name]}
    color={color}
  />;
}

Icon.propTypes = {
  name: PropTypes.string,
  color: PropTypes.string,
};

Icon.defaultProps = {
  name: '',
  color: Color.silver,
};

export default Icon;
