export async function getVideoLinkFromJSONUrl(url) {
  let videoUrl = '';
  let response = await fetch(url);
  let data = await response.json();
  if (data.length > 0) {
    videoUrl = data.find(item => getFileExtension(item) === 'mp4');
  }
  return videoUrl;
}


export function getFileExtension(fileUrl) {
  return fileUrl.slice((fileUrl.lastIndexOf(".") - 1 >>> 0) + 2);
}