import React, {Component} from 'react';
import {connect} from 'react-redux'
import {
  Button,
  VideoPlayer
} from '../../components';
import Color from '../../themes/Colors';
import {Link} from "react-router-dom";
import {UserAction} from '../../actions'
import shallowequal from 'shallowequal';

import './styles.scss'
import ContentItem from './components/ContentItem'
import EditContentModal from './components/EditContentModal';


class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collection: props.userReducer.collection || [],
      isShowModalEdit: false,
      editContent: null,
      playingContent: {},
      isShowVideoModal: false
    }
  }

  componentWillReceiveProps(nextProps) {
    if (!shallowequal(nextProps.userReducer.collection, this.props.userReducer.collection)) {
      this.setState({
        collection: nextProps.userReducer.collection
      })
    }
  }

  handleDeleteContent = (id) => {
    this.props.onDelete(id)
  };

  handleFavoriteContent = (id) => {
    this.props.onFavorite(id)
  };

  handleEditContent = (content) => {
    this.props.onEdit(content);
    this.handleCloseEditModal();
  };

  handleShowEditModal = (content) => {
    this.setState({
      editContent: content,
      isShowModalEdit: true
    })
  };

  handleCloseEditModal = () => {
    this.setState({
      editContent: null,
      isShowModalEdit: false
    })
  };

  handlePlayVideo = (content) => {
    this.setState({
      playingContent: content,
      isShowVideoModal: true
    })
  };

  handleCloseVideo = () => {
    this.setState({
      playingContent: null,
      isShowVideoModal: false
    })
  };

  _renderCollection = () => {
    const {collection} = this.state;
    return (
      <div className='grid-wrapper'>
        {
          collection.map((item, index) =>
            <ContentItem
              key={index}
              content={item}
              onFavorite={this.handleFavoriteContent}
              onEdit={this.handleShowEditModal}
              onDelete={this.handleDeleteContent}
              onPlayVideo={() => this.handlePlayVideo(item)}
            />
          )
        }
      </div>
    );
  };

  render() {
    const {isShowVideoModal, isShowModalEdit, editContent, playingContent} = this.state;
    return (
      <div className='home-container'>
        <div className='home-header'>
          <div className='home-title'>NASA Collection</div>
          <Link to="/nasa-collection/nasa-search">
            <Button
              icon='plus'
              iconColor={Color.white}
              className='btn btn-primary'
              text={'Add new item'}
            />
          </Link>
        </div>
        <VideoPlayer
          onClose={this.handleCloseVideo}
          visible={isShowVideoModal}
          content={playingContent}
        />
        {this._renderCollection()}
        <EditContentModal
          visible={isShowModalEdit}
          content={editContent}
          onSubmit={this.handleEditContent}
          onClose={this.handleCloseEditModal}
        />
      </div>
    );
  }
}

// Define which part of the state we're passing to this component
const
  mapStateToProps = state => ({
    userReducer: state.UserReducer
  });

// Define the actions this component may dispatch
const
  mapDispatchToProps = (dispatch) => {
    return {
      onDelete: (id) => {
        dispatch(UserAction.removeFromCollection(id))
      },
      onFavorite: (id) => {
        dispatch(UserAction.favoriteContent(id))
      },
      onEdit: (content) => {
        dispatch(UserAction.editContent(content))
      },
    }
  };

export default connect(mapStateToProps, mapDispatchToProps)(Home)
