export default class Content {
  constructor(data = {}, initModel = true) {
    if (initModel) {
      this.init(data);
    }
  }

  init(data) {
    const metaData = data.data[0] || {};
    const links = data.links;

    this.id = metaData.nasa_id || 0;
    this.title = metaData.title || '';
    this.description = metaData.description || '';
    this.type = metaData.media_type || '';
    this.linkPreview = links[0].href || '';
    this.linkFile = data.href;
    this.author = metaData.center || '';
    this.created = metaData.date_created || '';
    this.isFavorited = metaData.favorited || false;
    this.jsonUrl = data.href || ''
  }
}
