import React, {Component} from 'react';
import {
  Button,
  Modal,
  Textarea,
  InputField
} from '../../../../components';
import Color from '../../../../themes/Colors';
import shallowequal from 'shallowequal';

import './styles.scss'

class EditContentModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      content: props.content
    }
  }

  onChangeFieldValue = (fieldName, value) => {
    this.setState({
      content: {
        ...this.state.content,
        [fieldName]: value
      }
    })
  };

  componentWillReceiveProps(nextProps) {
    if (!shallowequal(nextProps.content, this.props.content)) {
      this.setState({
        content:nextProps.content
      });
    }
  }

  handleSubmit = () => {
    const {content} = this.state;
    this.props.onSubmit && this.props.onSubmit(content);
  }

  render() {
    const {visible, onClose} = this.props;
    const {content} = this.state;
    return (
      <Modal
        visible={visible}
        onClose={onClose}
        title="Edit">
        <div className='form-wrapper'>
          <InputField
            onChangeText={(event) => this.onChangeFieldValue('title', event.target.value)}
            label="Title"
            value={content && content.title}/>
          <Textarea
            onChangeText={(event) => this.onChangeFieldValue('description', event.target.value)}
            label="Description"
            value={content && content.description}
          />
          <InputField
            onChangeText={(event) => this.onChangeFieldValue('type', event.target.value)}
            label="Type"
            disabled
            value={content && content.type}
          />
          <InputField
            onChangeText={(event) => this.onChangeFieldValue('linkPreview', event.target.value)}
            label="Link preview image url"
            required
            value={content && content.linkPreview}/>
          <InputField
            onChangeText={(event) => this.onChangeFieldValue('linkFile', event.target.value)}
            label="Link file url"
            required
            value={content && content.linkFile}/>
          <div>
            <Button
              onClick={this.handleSubmit}
              text="Save"
              className='btn btn-primary'
              icon='checked'
              iconColor={Color.white}/>
          </div>
        </div>
      </Modal>
    );
  }
}

export default EditContentModal;
