import React, {Component} from 'react';
import {
  Button,
  Icon,
} from '../../../../components';
import Color from '../../../../themes/Colors';
import moment from 'moment';

import './styles.scss'

class ContentItem extends Component {
  // constructor(props) {
  //   super(props);
  // }

  handleDeleteContent = () => {
    const {content, onDelete} = this.props;
    onDelete && onDelete(content.id);
  };

  handleFavoriteContent = () => {
    const {content, onFavorite} = this.props;
    onFavorite && onFavorite(content.id);
  };

  handleEditContent = () => {
    const {content, onEdit} = this.props;
    onEdit && onEdit(content);
  };

  render() {
    const {content, onPlayVideo} = this.props;
    return (
      <div className='content-item-wrapper'>
        <div onClick={onPlayVideo} className='thumb-wrapper'>
          <img
            src={content.linkPreview}
            alt="Italian Trulli"/>
          <div className='play-icon'>
            <Icon name='play' color={Color.black}/>
          </div>
        </div>
        <div className='item-header'>
          <div>
            {content.author}
          </div>
          <div>
            {moment(content.created).format('DD MMM, YYYY')}
          </div>
        </div>
        <div className='title-wrapper'>
          {content.title}
        </div>
        <div className='description-wrapper'>
          {content.description}
        </div>
        <div className='footer-wrapper'>
          <div>
            <Button
              onClick={this.handleFavoriteContent}
              className='btn btn-light'
              icon='heart'
              iconColor={content.isFavorited ? Color.carrot : Color.silver}
            />
          </div>
          <div>
            <Button
              onClick={this.handleDeleteContent}
              className='btn btn-light'
              icon='trash'
              iconColor={Color.silver}
            />
          </div>
          <div>
            <Button
              onClick={this.handleEditContent}
              className='btn btn-light'
              icon='edit'
              iconColor={Color.silver}
            />
          </div>
        </div>

      </div>
    );
  }
}

export default ContentItem;
