import React, {Component} from 'react';
import {
  Button,
  Icon,
} from '../../../../components';
import Color from '../../../../themes/Colors';
import './styles.scss';
import moment from 'moment';


class SearchItem extends Component {

  render() {
    const {content, onClickAddBtn, isAdded, onPlayVideo} = this.props;
    return (
      <div className='search-item-wrapper'>
        <div onClick={onPlayVideo} className='thumb-wrapper'>
          <img
            src={content.linkPreview}
            alt="Italian Trulli"/>
          <div className='play-icon'>
            <Icon name='play' color={Color.black}/>
          </div>
        </div>
        <div className='item-header'>
          <div>
            {content.author}
          </div>
          <div>
            {moment(content.created).format('DD MMM, YYYY')}
          </div>
        </div>
        <div className='title-wrapper'>
          {content.title}
        </div>
        <div className='description-wrapper'>
          {content.description}
        </div>
        <div>
          <Button
            onClick={onClickAddBtn}
            className='btn btn-light btn-block'
            disabled={isAdded}
            icon={isAdded ? 'checked' : 'plus'}
            text={isAdded ? 'Added content' : 'Add to NASA collection'}
          />
        </div>
      </div>
    );
  }
}

export default SearchItem;
