import {actionTypes} from '../constants';

const initialState = {
  collection: []
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.USER_ADD_TO_COLLECTION: {
      return {
        ...state,
        collection: [action.payload, ...state.collection]
      };
    }

    case actionTypes.USER_REMOVE_FROM_COLLECTION: {
      const updatedCollection = state.collection.filter(item => item.id !== action.id);
      return {
        ...state,
        collection: updatedCollection
      };
    }

    case actionTypes.USER_EDIT_FROM_COLLECTION : {
      const editContent = action.payload;
      const itemIndex = state.collection.map(item => {
        return item.id
      }).indexOf(editContent.id);
      const updatedCollection = [...state.collection];
      if (itemIndex > -1) {
        updatedCollection[itemIndex] = editContent;
      }
      return {
        ...state,
        collection: updatedCollection
      };
    }

    case actionTypes.USER_FAVORITE_FROM_COLLECTION: {
      const contentId = action.id;
      const itemIndex = state.collection.map(item => {
        return item.id
      }).indexOf(contentId);
      const updatedCollection = [...state.collection];
      if (itemIndex > -1) {
        updatedCollection[itemIndex].isFavorited = !updatedCollection[itemIndex].isFavorited;
      }
      return {
        ...state,
        collection: updatedCollection
      };
    }

    default:
      return state;
  }
};
