import * as urls from './urls';
import request from './request';

export function searchFromNasa(keyword) {
  let requestURL = urls.SEARCH_FROM_NASA;
  if (keyword.length > 0) {
    requestURL = requestURL.replace('{keyword}', keyword);
  }
  const option = {
    method: 'GET',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
  };
  return request(requestURL, option);
}