/**
 *
 * Button
 *
 */
import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './styles.scss';
import Icon from '../Icon';
import Color from '../../themes/Colors';
import {getVideoLinkFromJSONUrl} from '../../utils/common';
import shallowequal from 'shallowequal';

class VideoPlayer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      videoUrl: ''
    }
  }

  async componentWillReceiveProps(nextProps) {
    if (nextProps.content && !shallowequal(nextProps.content, this.props.content)) {
      const videoUrl = await getVideoLinkFromJSONUrl(nextProps.content.linkFile);
      this.setState({videoUrl})
    } else {
      this.setState({videoUrl: ''})
    }
  }

  render() {
    const {visible, onClose, content} = this.props;
    const {videoUrl} = this.state;
    return (
      <div className={visible ? 'video-modal-showing' : 'video-modal-hidden'}>
        <div className='video-modal-content'>
          <div className='video-modal-header'>
          <span onClick={onClose} className="video-close">
            <Icon name='close' color={Color.cloud}/>
          </span>
            <div className='modal-title'>{content && content.title}</div>
          </div>
          <div className='video-modal-body'>
            {visible && videoUrl && <video width="100%" controls>
              <source src={videoUrl}/>
              Your browser does not support HTML5 video.
            </video>}
          </div>
        </div>
      </div>
    );
  }
}

VideoPlayer.propTypes = {
  visible: PropTypes.bool,
  content: PropTypes.object,
  onClose: PropTypes.func
};
VideoPlayer.defaultProps = {
  visible: false,
  content: null,
  onClose: () => {
  }
};
//
export default VideoPlayer;