import {put, takeLatest} from 'redux-saga/effects';
import {SearchAction} from '../actions';
import {actionTypes} from '../constants';
import * as SearchApi from '../apis/SearchApi';
import Content from '../models/Content';

export function* getSearchResult(action) {
  try {
    const {keyword} = action;
    const response = yield SearchApi.searchFromNasa(keyword);
    if (response.collection) {
      let results = [];
      if (response.collection.items.length > 0) {
        results = response.collection.items.map(item => new Content(item))
      }
      yield put(SearchAction.searchSuccess(results));
    } else {
      yield put(SearchAction.searchfailure({message: response.message}));
    }
  } catch (err) {
    yield put(SearchAction.searchfailure(err));
  }
}

export default function* saga() {
  yield takeLatest(actionTypes.SEARCH_REQUEST, getSearchResult);
}
