import React, {Component} from 'react';
import {connect} from 'react-redux'
import {
  InputField,
  Loading,
  VideoPlayer,
  Icon,
} from '../../components';
import Color from '../../themes/Colors';
import {Link} from "react-router-dom";
import SearchItem from './components/SearchItem';
import AddCollectionModal from './components/AddCollectionModal';
import {SearchAction, UserAction} from '../../actions'
import shallowequal from 'shallowequal';
import './styles.scss'


class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keyword: props.searchReducer.keyword || '',
      searchResults: props.searchReducer.searchResults || [],
      loading: props.searchReducer.loading || false,
      isShowModalAdd: false,
      addContent: null,
      isShowVideoModal: false,
      playingContent: null
    };
    this.searchDelay = 0;
  }

  componentWillReceiveProps(nextProps) {
    if (!shallowequal(nextProps.searchReducer.searchResults, this.props.searchReducer.searchResults)) {
      this.setState({
        searchResults: nextProps.searchReducer.searchResults
      })
    }
  }

  handleOnSearch = (event) => {
    const text = event.target.value;
    this.setState({keyword: text});

    if (this.searchDelay) {
      clearTimeout(this.searchDelay);
    }

    if (text) {
      this.searchDelay = setTimeout(() => {
        this.props.onSearch(text);
      }, 500)
    }
  };

  handleShowModalAdd = (content) => {
    this.setState({
      addContent: content,
      isShowModalAdd: true
    })
  };

  handleCloseAddModal = () => {
    this.setState({
      addContent: null,
      isShowModalAdd: false
    })
  };

  handleAddToCollection = (content) => {
    this.props.onAddToCollection(content);
    this.handleCloseAddModal();
  };

  handlePlayVideo = (content) => {
    this.setState({
      playingContent: content,
      isShowVideoModal: true
    })
  };

  handleCloseVideo = () => {
    this.setState({
      playingContent: null,
      isShowVideoModal: false
    })
  }

  _renderLoading = () => {
    return (
      <div className='loading-wrapper'>
        <Loading/>
      </div>
    );
  }

  _renderSearchResult = () => {
    const {searchResults} = this.state;
    const {collection} = this.props.userReducer;
    return (
      <div className='grid-wrapper'>
        {searchResults.map((result, index) =>
          <SearchItem
            key={index}
            onPlayVideo={() => this.handlePlayVideo(result)}
            onClickAddBtn={() => this.handleShowModalAdd(result)}
            content={result}
            isAdded={collection.findIndex(item => item.id === result.id) > -1}
          />)}
      </div>

    );
  };

  render() {
    const {searchResults, keyword, isShowVideoModal, playingContent} = this.state;
    const {searchReducer} = this.props;
    return (
      <div className='search-container'>
        <div>
          <Link className='back-link' to="/nasa-collection">
            <div><Icon name='back' color={Color.wisteria}/> Back to Collection</div>
          </Link>
          <div className='header-title'>Search from Nasa</div>
          <div className='search-box'>
            <InputField
              value={keyword}
              placeholder="Search..."
              onChangeText={this.handleOnSearch}/>
          </div>
          {
            searchReducer.keyword && !searchReducer.loading ?
              <div className='search-result'>{searchResults.length} result for "{searchReducer.keyword}"</div>
              :
              null
          }
        </div>
        {searchReducer.loading ? this._renderLoading() : this._renderSearchResult()}
        <AddCollectionModal
          content={this.state.addContent}
          onClose={this.handleCloseAddModal}
          visible={this.state.isShowModalAdd}
          onSubmit={this.handleAddToCollection}
        />
        <VideoPlayer
          onClose={this.handleCloseVideo}
          visible={isShowVideoModal}
          content={playingContent}
        />
      </div>
    );
  }
}

// Define which part of the state we're passing to this component
const
  mapStateToProps = state => ({
    searchReducer: state.SearchReducer,
    userReducer: state.UserReducer
  });

// Define the actions this component may dispatch
const
  mapDispatchToProps = (dispatch) => {
    return {
      onSearch: (keyword) => {
        dispatch(SearchAction.searchRequest(keyword))
      },
      onAddToCollection: (content) => {
        dispatch(UserAction.addToCollection(content))
      }
    }
  };

export default connect(mapStateToProps, mapDispatchToProps)(Search)