import {actionTypes} from '../constants';

export function addToCollection(content) {
  return {
    type: actionTypes.USER_ADD_TO_COLLECTION,
    payload: content
  };
}

export function editContent(content) {
  return {
    type: actionTypes.USER_EDIT_FROM_COLLECTION,
    payload: content
  };
}

export function removeFromCollection(id) {
  return {
    type: actionTypes.USER_REMOVE_FROM_COLLECTION,
    id
  };
}

export function favoriteContent(id) {
  return {
    type: actionTypes.USER_FAVORITE_FROM_COLLECTION,
    id
  };
}
